<template>
    <div class="parent position-relative h-100 p-0 m-0 border border-0">

        <LoaderComponent :show="showLoader" :percentLoaded="percentLoaded"></LoaderComponent>

        <HotspotDetails :types-colors="typesColors" :canShow="showHsDetails" :hsData="hsData"
            @onCloseDetails="showHsDetails = false">
        </HotspotDetails>

        <OrbitalMenu :stage-count="stageCount" :starting-at="startStage" @on-play="play" @on-step="step"
            @on-set-level="setLevel" @on-show-filter="showFilter" @on-zoom="setZoom" @on-zoom-end="zoomEnd">
        </OrbitalMenu>

        <div class="orbital-canvas p-0 m-0 border border-0 h-100 position-relative" id="orbital">
            <PopoverComponent :types-colors="typesColors"></PopoverComponent>
        </div>

        <FilterComponent :colors-types="typesColors" :default-filters="defaultFilters" :show-toast="showToast"
            @onCloseToast="closeFilter" @onSurfaceChanged="surfaceChanged" @onTypesChanged="typesChanged">
        </FilterComponent>

    </div>
</template>



<script setup>

import { ref, computed, onMounted, onUnmounted } from "vue";

import { Orbital } from "@/canvas-modules/orbital";
import PopoverComponent from "./PopoverComponent";
import FilterComponent from "./FilterComponent";
import LoaderComponent from "./LoaderComponent";
import HotspotDetails from "./HotspotDetails";
import OrbitalMenu from "./OrbitalMenu";


let orbital = null;

const showToast = ref(false),

    showLoader = ref(true),

    showHsDetails = ref(false),

    hsData = ref({}),

    stageCount = ref(1),

    typesColors = ref({}),

    percentLoaded = ref(0);

const defaultFilters = ref({});

const startingStage = ref(0);

const startStage = computed(() => {
    return Math.min(Math.max(parseInt(startingStage.value), 0), stageCount.value - 1);
});

function init() {
    orbital = new Orbital("orbital");
    orbital.init();

    stageCount.value = orbital.getParam("animationsCount");
    startingStage.value = orbital.getParam("animStart");

    typesColors.value = orbital.getHSParam("color_types");
    defaultFilters.value = orbital.getHSParam("default_filters");

    window.addEventListener("OnLoadingChange", onLoadingChange);
    window.addEventListener("OnAppReady", onAppReady);
    window.addEventListener("OnHSClick", onHSClick);
}



function setLevel(level) {
    orbital.setLevel(level);
}

function play(play) {
    if (play) orbital.playOrbital();
    else orbital.stopOrbital();
}

function step(reverse) {
    if (reverse)
        orbital.prev();
    else
        orbital.next()
}

let interval = undefined;
function setZoom(zoomIn) {
    window.clearInterval(interval);
    orbital.setZoom(zoomIn);
    interval = window.setInterval(() => orbital.setZoom(zoomIn), 30);
}
function zoomEnd() {
    window.clearInterval(interval);
}


function showFilter() {
    showToast.value = true;
}

function closeFilter() {
    showToast.value = false;
}

function surfaceChanged(surface) {
    orbital.filterHSByPrice(surface);
}

function typesChanged(types) {
    orbital.filterHSByTypes(types);
}

function clearEvents() {
    window.removeEventListener("OnLoadingChange", onLoadingChange);
    window.removeEventListener("OnAppReady", onAppReady);
    window.removeEventListener("OnHSClick", onHSClick);
}
/**
 * Events functions
 */

function onLoadingChange() {
    percentLoaded.value = orbital.getPercentLoaded();
}

function onAppReady() {
    showLoader.value = false;
}

function onHSClick(e) {
    const detail = e.detail;
    const region = detail.region;
    if (region === undefined) return;

    hsData.value = region;
    showHsDetails.value = true;
}



onMounted(() => {
    init();
})

onUnmounted(() => {
    clearEvents();
    orbital.unmountOrbital();
})

</script>


<style lang="scss" scoped></style>
