<template>
    <div class="filter position-absolute top-50 start-50 translate-middle p-0" ref="filterParent">
        <div class="d-flex justify-content-center">

            <div id="filter" class="toast" role="alert" aria-live="assertive" aria-atomic="true" data-bs-autohide="false"
                ref="filter">
                <div class="toast-header bg-dark text-white">
                    <strong class="me-auto">Filtrage de Lot</strong>
                    <button type="button" class="btn-close btn-close-white" @click="closeToast"></button>
                </div>
                <div class="toast-body">

                    <slider-filter @onSurfaceChanged="surfaceChanged" :min="49" :max="66.9" :step="0.1" :gap="2"
                        :slider-delay="300">
                    </slider-filter>

                    <hr />

                    <types-filter :colors-types="colorsTypes" :default-types="checkedTypes"
                        @onTypesChanged="typesChanged"></types-filter>

                </div>
            </div>
        </div>
    </div>
</template>


<script setup>

import { Toast } from "bootstrap";

import SliderFilter from "@/components/orbital/search/SliderFilter";
import TypesFilter from "@/components/orbital/search/TypesFilter";

import { onClickOutside } from '@vueuse/core'

import { ref, reactive, watch, onMounted, onUnmounted } from "vue";


const emits = defineEmits([
    "onCloseToast",
    "onSurfaceChanged",
    "onTypesChanged"
]);


const props = defineProps(
    {
        showToast: {
            type: Boolean,
            default: true
        },

        colorsTypes: Object,

        defaultFilters: {
            type: Object,
            default: () => {
                return {
                    types: [],
                    stages: [],
                    status: []
                };

            }
        }
    }
);


const filterParent = ref(null);
const filter = ref(null);

onClickOutside(filterParent, () => {
    closeToast();
});

let toast = null;
const appReady = ref(false);
const surface = reactive({
    min: 49,
    max: 66,
});
const checkedTypes = ref([]);





function initToast() {
    toast = new Toast(filter.value);
    toogleToast();
}
function toogleToast() {
    if (props.showToast) {
        toast.show();
    } else {
        toast.hide();
    }
}

function closeToast() {
    emits("onCloseToast");
}


function surfaceChanged(sur = surface) {

    if (appReady.value) {
        surface.min = sur.min;
        surface.max = sur.max;
        emits("onSurfaceChanged", surface);
    }
}

function typesChanged(types = props.defaultFilters.types) {
    if (appReady.value) {
        checkedTypes.value = types;
        emits("onTypesChanged", checkedTypes.value);
    }
}

function _appReady() {
    appReady.value = true;
    surfaceChanged();
    typesChanged();
}


watch(() => props.showToast, () => {
    toogleToast();
});


onMounted(() => {
    initToast();
    window.addEventListener("OnAppReady", _appReady)
});

onUnmounted(() => {
    window.removeEventListener("OnAppReady", _appReady)
});

</script>



<style lang="scss" scoped>
.filter {
    z-index: 1030;
    width: auto;

    .toast {
        width: 25em;

        .toast-header {
            letter-spacing: .13em;
            font-size: .9em;
            font-weight: 500;
        }

    }
}
</style>